@import "../../styles/sizes";

#News {

  a, a:link, a:visited {
    text-decoration: none;
  }

  .news-info {
    font-size: x-small;
  }

  .news-image {
    object-fit: cover;
    height: 100%;
    width: 100%;
  }

  .uk-slider-container {
    padding: 0 0 50px 0
  }

  .news-body-image {
    overflow: hidden;
    height: 128px;
  }

  .uk-card-body {
    padding: 15px;
  }

  .uk-card-title {
    font-size: medium;
  }

  .news-text {
    margin-top: 20px;
    font-size: x-small;
  }

  .news-body-top {
    height: 40px;
  }

  .news-body-bottom {
    height: 40px;
    margin-top: 50px;
  }

  @media screen and (min-width: $ml) {
    .news-info {
      font-size: medium;
    }

    .news-body-top {
      height: 80px;
    }

    .uk-card-body {
      padding: 20px;
    }

    .news-body-bottom {
      height: 50px;
      margin-top: 50px;
    }

    .news-body-image {
      overflow: hidden;
      height: 200px;
    }

    .uk-slider-container {
      padding: 0 25px 50px 25px
    }
  }

  @media screen and (min-width: $l) {
    .article-info {
      font-size: large;
    }
  }
}